import {customElement} from '@here/hds-base';
import {html, LitElement} from 'lit';
import {property} from 'lit/decorators.js';
import {ifDefined} from 'lit/directives/if-defined.js';
import {styleMap} from 'lit/directives/style-map.js';
import {styles} from './hds-tab-panel.css.js';

/**
 * @slot default Main content related to the corresponding tab
 */
@customElement('hds-tab-panel')
export class TabPanel extends LitElement {
  static styles = styles;

  @property({type: Boolean, reflect: true, attribute: false}) show = false;
  @property({type: String, reflect: true, attribute: 'aria-labelledby'}) ariaLabelledby = '';

  protected render() {
    const panelStyles = {
      display: '',
    };
    if (!this.show) {
      panelStyles.display = 'none';
    }
    return html`
      <div
        class="hds-tab-panel-wrapper"
        role="tabpanel"
        aria-labelledby="${ifDefined(this.ariaLabelledby)}"
        style=${styleMap(panelStyles)}
      >
        <slot></slot>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-tab-panel': TabPanel;
  }
}
